import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import $ from 'jquery';
import * as serviceWorker from './serviceWorker';

ReactDOM.render(<App />, document.getElementById('root'));
let winWidth; // ERROROROROR
$( window ).resize(function() {
  winWidth= $( window ).width();
});

if(winWidth < 1200){
	$('.trees').wrap('<div class="treeWrap"></div>');
} else {
	$('.trees').hover( function(e){
		const thisId= $(this).attr('data-id');
		const dataObject= {
      "display":"<ul><li>HTML</li><li>CSS</li><li>Framework7</li></ul>",
			"tags":"<ul><li>HTML</li><li>CSS</li><li>Framework7</li><li>Adobe Illustrator</li></ul>",
			"maps":"<ul><li>HTML</li><li>CSS</li><li>jQuery</li><li>Adobe Illustrator</li></ul>",
      "ocfl":"<ul><li>HTML</li><li>CSS</li><li>jQuery</li><li>Adobe Photoshop</li></ul>",
			"php":"<ul><li>PHP</li><li>JS</li></ul>",
			"any":"<ul><li>HTML</li><li>CSS</li><li>jQuery</li></ul>",
		};
			$('.project-content').html(dataObject[thisId]);
	}, function(e){
		$('.project-content').html('');
	});
}
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
