import React, { Component } from 'react';
import $ from 'jquery';

class VideoPlayer extends Component {
  playVideo() {
    if (this.refs.phpVidRef.paused === false) {
        this.refs.phpVidRef.pause();
        $('.playButton').fadeTo("fast",1);
    } else {
        this.refs.phpVidRef.play();
        $('.playButton').fadeTo("fast",0);
    }
  }

  render() {
    const source = this.props.src;
    return (
      <React.Fragment>
        <div
          className="video"
          onClick={this.playVideo.bind(this)}>
          <video
            ref="phpVidRef"
            src={source}
            type="video/mp4" />
          <div className="playButton" />
        </div>
      </React.Fragment>
    );
  }
}

export default VideoPlayer;
